import React from 'react';
import { Link } from 'react-router-dom';
import { Form,Option,Button,Container,Navbar,Nav,NavDropdown,Row,Col,ListGroup  } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Component } from 'react';


const BookCard = (props) => {
    const  book  = props.book;
    let send

    if (book.send == "1")
    {

send = <Container fluid="sm">
        <Row fluid="sm" className="bg-success d-flex-inline p-3 border border-light"> 
            <Col md={5}  className="text-light">{book.name}</Col>
            <Link to={`/Detail/${book._id}`}>q</Link>
            <Col md={5} className="text-light">{book.score}</Col>
    </Row>
    </Container>

    }
    else
    {
send = <Container fluid="sm">
        <Row fluid="sm" className="bg-dark d-flex-inline p-3 border border-light"> 
            <Col md={5}  className="bg-dark text-light">{book.name}</Col>
            <Link to={`/Detail/${book._id}`}>q</Link>
            <Col md={5} className="bg-dark text-success">{book.score}</Col>
    </Row>
    </Container>
    }

    

    return(
        <div>{send}</div>
    )
};

export default BookCard;