import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Form,Option,Button,Container,Navbar,Nav,NavDropdown,Row,Col,Card} from 'react-bootstrap';

class Detailbook extends Component {
  constructor(props) {
    super(props);
    this.state = {
        name: "",
      email: "",
      society:"",
      time: "",
      publish_date: "",
      q1: "",
      q2: "",
      q3: "",
      q4: "",
      score: "",
      book: {},
      score:"",
      send:" "
    };
  }

  componentDidMount() {
    // console.log("Print id: " + this.props.match.params.id);
    axios
      .get('/api/auth/detailp/'+this.props.match.params.id)
      .then(res => {
        // console.log("Print-showBookDetails-API-response: " + res.data);
        this.setState({
            name: res.data.name,
      email: res.data.email,
      society:res.data.society,
      time: res.data.time,
      publish_date: res.data.publish_date,
      q1: res.data.q1,
      q2: res.data.q2,
      q3: res.data.q3,
      q4: res.data.q4,
      score:res.data.score,

          book: res.data
        })
        console.log(this.state.q3)
      })
      .catch(err => {
        console.log("Error from ShowBookDetails");
      })
  }
      onChange = e => {
        this.setState({ [e.target.name]: e.target.value });
      };
    
      onSubmit = e => {
        e.preventDefault();
    
        const data = {
            name:this.state.name,
      email: this.state.email,
      society:this.state.society,
      time: this.state.time,
      publish_date: this.state.publish_date,
      q1: this.state.q1,
      q2: this.state.q2,
      q3: this.state.q3,
      q4: this.state.q4,
          score: this.state.score,
          
          };
    
        axios
          .put('/api/auth/score/'+this.props.match.params.id, data)
          .then(res => {

    
            
    
            
            
            
          })
          .catch(err => {
            this.setState({error:err.response.data.error});
          })
        };

        pppp = e => {
          e.preventDefault();
      
          const data = {
              name:this.state.name,
        email: this.state.email,
        society:this.state.society,
        time: this.state.time,
        publish_date: this.state.publish_date,
        q1: this.state.q1,
        q2: this.state.q2,
        q3: this.state.q3,
        q4: this.state.q4,
            score: this.state.score,
            send: "1"
            
            };
      
          axios
            .put('/api/auth/send/'+this.props.match.params.id, data)
            .then(res => {
  
      
              
      
              
              
              
            })
            .catch(err => {
              this.setState({error:err.response.data.error});
            })
          };


  

  


  render() {

    const book = this.state.book;
    let BookItem = <div>
      <table className="table table-hover table-dark">
        {/* <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">First</th>
            <th scope="col">Last</th>
            <th scope="col">Handle</th>
          </tr>
        </thead> */}
        <tbody>
          <tr>
            <th scope="row">1</th>
            <td>Title</td>
            <td>{ book.name }</td>
          </tr>
          <tr>
            <th scope="row">2</th>
            <td>Author</td>
            <td>{ book.email }</td>
          </tr>
          <tr>
            <th scope="row">3</th>
            <td>ISBN</td>
            <td>{ book.society }</td>
          </tr>
          <tr>
            <th scope="row">4</th>
            <td>Publisher</td>
            <td>{ book.time}</td>
          </tr>
          <tr>
            <th scope="row">5</th>
            <td>Published Date</td>
            <td>{ book.q1 }</td>
          </tr>
          <tr>
            <th scope="row">6</th>
            <td>Description</td>
            <td>{ book.q2 }</td>
          </tr>
          <tr>
            <th scope="row">6</th>
            <td>Description</td>
            <td>{ book.score }</td>
          </tr>
        </tbody>
      </table>
    </div>

    return (
      <div className="ShowBookDetails">
        <div className="container">
          <div className="row">
            <div className="col-md-10 m-auto">
              <br /> <br />
              <Link to="/" className="btn btn-outline-warning float-left">
                  Show Book List
              </Link>
            </div>
            <br />
            <div className="col-md-8 m-auto">
              <h1 className="display-4 text-center">Book's Record</h1>
              <p className="lead text-center">
                  View Book's Info
              </p>
              <hr /> <br />
            </div>
          </div>
          <div>
            { BookItem }
          </div>

          

            <div className="col-md-6">
              <Link to={`/edit-book/${book._id}`} className="btn btn-outline-info btn-lg btn-block">
                    Edit Book
              </Link>
              <br />

              <Container fluid="sm">
      
      <Form onSubmit={this.onSubmit}> 
      <Form.Group className="mb-3">
      <Form.Label className="text-light">Email address</Form.Label>
      
      <Form.Control   type='text'
                    placeholder='Title of the Book'
                    name='score'
                    className='form-control'
                    value={this.state.score}
                    onChange={this.onChange}
      />
    
    </Form.Group>
    </Form>
    </Container>

    <Container fluid="sm">
      
      <Button onClick={this.pppp}>send</Button>
    </Container>




            </div>

            

          
            {/* <br />
            <button type="button" class="btn btn-outline-info btn-lg btn-block">Edit Book</button>
            <button type="button" class="btn btn-outline-danger btn-lg btn-block">Delete Book</button> */}

        </div>
      </div>
    );
  }
}

export default Detailbook;
