import { useState, useEffect } from "react";
import axios from "axios";
import "./App.css"
import { Link } from "react-router-dom";
import "./LoginScreen.css";
import { Navbar,Container,Nav,NavDropdown,Row,Col } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

const LoginScreen = ({ history }) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  useEffect(() => {
    if (localStorage.getItem("authToken")) {
      history.push("/");
    }
  }, [history]);

  const loginHandler = async (e) => {
    e.preventDefault();

    const config = {
      header: {
        "Content-Type": "application/json",
      },
    };

    try {
      const { data } = await axios.post(
        "/api/auth/login",
        { email, password },
        config
      );

      localStorage.setItem("authToken", data.token);

      history.push("/");
    } catch (error) {
      setError(error.response.data.error);
      setTimeout(() => {
        setError("");
      }, 5000);
    }
  };
  return(
    <div>
      <Navbar bg="dark" variant="dark" className=" ml-0 border-bottom border-primary border-4" expand="md">
  <Container className="ms-0">
    <Navbar.Brand className="text-light" href="#home">React-Bootstrap</Navbar.Brand>
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="me-auto text-light">
        <Nav.Link  className="text-light" href="/login">Home</Nav.Link>
        <Nav.Link  className="text-light" href="/QQQ">societies</Nav.Link>
        <Nav.Link  className="text-light" href="/Listq">list</Nav.Link>
    </Nav>
    </Navbar.Collapse>
  </Container>
</Navbar>
  
  {/* main image & intro text */}
  <section>
    <div className="container-fluid" style={{height: '100vh'}}>
      <Row className="justify-content-center align-items-center bg-light"style={{height: '100vh'}}>
        <Col xs={{order:2}} md={{span:6, order:1}} className="text-center d-flex justify-content-center align-items-center" style={{height: '100vh'}}>
          <div className="border border-light border-1 rounded-3 shadow-lg" style={{width: '500px'}}>
            <Row g={3}>
            <form className="needs-validation d-flex justify-content-center " noValidate>
              <Col md={9}>
              
                <label htmlFor="email" className="form-label">Email</label>
                <input type="email"
            required
            id="email"
            placeholder="Email address"
            onChange={(e) => setEmail(e.target.value)}
            value={email}
            tabIndex={1} className="form-control" required />
                <div className="valid-feedback">
                  Looks good!
                </div>
              </Col>
            </form>
            </Row>
            <Row g={3}>
            <form className="row g-3 needs-validation d-flex justify-content-center" noValidate>
              <Col md={9}>
              
                <label htmlFor="validationCustom02" className="form-label">Password</label>
                <input
            type="password"
            required
            id="password"
            autoComplete="true"
            placeholder="Enter password"
            onChange={(e) => setPassword(e.target.value)}
            value={password}
            tabIndex={2}
          className="form-control"required />
                <div className="valid-feedback">
                  Looks good!
                </div>
              </Col>
            </form>
            </Row>

            <Row g={3}>
            <form onSubmit={loginHandler}className="row g-3 needs-validation" noValidate>
              <div className="d-grid col-9 mx-auto">
                <button className="btn btn-primary" type="submit">login</button>
              </div>
            </form>
            </Row>
            <Row g={1}>
            <form className="row g-1 needs-validation" noValidate>
              <Col g={12}>
                or
              </Col>
            </form>
            </Row>
            <Row g={3}>
            <form className="row g-3 needs-validation" noValidate>
              <div className="d-grid col-9 mx-auto">
                <button className="btn btn-success" type="submit">continu</button>
              </div>
            </form>
            </Row>
          </div>
        </Col>
        <Col xs={{order:1}} md={{span:6, order:2}} className="text-center d-flex justify-content-center align-items-center bg-dark" style={{height: '100%'}}>
          <div>
            <img src="https://svg-clipart.com/clipart/logo/l6RLQj6-nike-logo-clipart.png" className="div" alt="ebook" />
            <h1>
              <div className="display-2 text-primary text-center">Black-Belt</div>
              <div className="display-5 text-muted text-center">Your Coding Skills</div>
            </h1>
            <p className="lead my-4 text-muted text-center">Lorem ipsum dolor sit amet consectetur adipisicing elit. Laboriosam, dignissimos?</p>
          </div>
        </Col>
      </Row>
    </div>
  </section>
</div>

  )

}

export default LoginScreen;