import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

// Routing
import PrivateRoute from "./components/routing/PrivateRoute";

import 'bootstrap/dist/css/bootstrap.min.css';
import $ from 'jquery';
import Popper from 'popper.js';
import 'bootstrap/dist/js/bootstrap.bundle.min';

import './App.scss';

// Screens
import PrivateScreen from "./components/screens/PrivateScreen";
import QQQ from "./components/screens/QQQ";
import PPPP from "./components/screens/PPPP";
import Mail from "./components/screens/Mail";
import Listq from "./components/screens/Listq";
import Detail from "./components/screens/Detail";
import Privatepage1 from "./components/screens/Privatepage1";
import Privatepage2 from "./components/screens/Privatepage2";
import LoginScreen from "./components/screens/LoginScreen";
import RegisterScreen from "./components/screens/RegisterScreen";
import ForgotPasswordScreen from "./components/screens/ForgotPasswordScreen";
import ResetPasswordScreen from "./components/screens/ResetPasswordScreen";


const App = () => {
  return (
    <Router>
      <div className="app">
        <Switch>
          <PrivateRoute exact path="/" component={PrivateScreen} />
          <Route exact path="/private/1/:resetToken" component={Privatepage1} />
          <Route exact path="/private/2/:resetToken" component={Privatepage2} />
          <Route exact path="/login" component={LoginScreen} />
          <Route exact path="/Mail/:p" component={Mail} />
          <Route exact path="/QQQ" component={QQQ} />
          <Route exact path="/PPPP/:q" component={PPPP} />
          <Route exact path="/Detail/:id" component={Detail} />
          <Route exact path="/Listq" component={Listq} />
          <Route exact path="/register" component={RegisterScreen} />
          <Route
            exact
            path="/forgotpassword"
            component={ForgotPasswordScreen}
          />
          <Route
            exact
            path="/passwordreset/:resetToken"
            component={ResetPasswordScreen}
          />
        </Switch>
      </div>
    </Router>
  );
};

export default App;
